import React, { useEffect } from 'react';
import { Routes, Route, useLocation } from 'react-router-dom';

// Bileenleri import et
import Navbar from './components/Navbar';
import Footer from './components/Footer';

// Sayfalar import et
import HomePage from './pages/HomePage';
// Dier sayfalar (oluturulunca import edilecek)
import GaleriPage from './pages/GaleriPage'; // YEN MPORT
import IletisimPage from './pages/IletisimPage'; // YEN MPORT
import { FloatingWhatsApp } from 'react-floating-whatsapp';



// const avatarUrl = "/sarissa-logo-avatar.png";
const avatarUrl = "https://ik.imagekit.io/46nvsdwq8/sefarenkatolyesi/sefarenkat%C3%B6lyesi_LOGO.svg";

// Sayfa deitiinde en ste scroll yapma bileeni
function ScrollToTop() {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
}


function App() {
  
  return (
    <div className="bg-white min-h-screen font-sans">
      <Navbar />
      <ScrollToTop />
      <main>
        <Routes>
          {/* Ana Sayfa */}
          <Route path="/" element={<HomePage />} />
          


          {/* Dier Sayfalar (oluturulunca eklenecek) */}
          <Route path="/gallery" element={<GaleriPage />} />
          <Route path="/contact" element={<IletisimPage />} />


        </Routes>
      </main>
      <Footer />
      
      <FloatingWhatsApp 
        phoneNumber="5550957358"
        accountName="Sefa Renk Atlyesi"
        statusMessage="15 dakika iinde cevaplarz!"
        chatMessage={"Merhaba! \nSefa Renk Atlyesine ho geldiniz nasl yardmc olabiliriz ?"}
        placeholder="Mesajnz yaznz..."
        avatar={avatarUrl}
        allowClickAway
        notification
        notificationSound
      />
    </div>
  );
}

export default App;
